.card {
    border: 4px solid;
    border-radius: 2px;
    width: 160px;
    height: 160px;
    margin: 1rem;
    position: relative;
    box-shadow: 2px 2px 5px rgb(56, 56, 56);
    transition: all .2s ease-in-out;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background:#ffffff;
  }
  
  .card > .img-container > img {
    height: 100%;
  }
  
  .card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
nav {
    width: 100%;
    font-size: 1.5rem;
    color: #fff;
    margin-bottom: 20px;
    background-color: purple;
    height: auto;
  }
  
  a {
    color: white;
    font-size: 2.6rem;
  }
  
  a:hover {
    color: pink;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 10px 0;
    width: 33.33%;
    text-align: center;
  }
  
  ul {
    padding-left: 0px;
  }

  .alignLeft{
    text-align: left;
    padding-left: 1%;
  }

  .alignRight{
    color: white;
    text-align: right;
    padding-right: 1%;
    font-size: 2.2rem;
  }
.wrapper {
    background: lavender;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
    padding-bottom: 50px;
  }
.title {
    width: 100%;
    text-align: right;
    font-size: 1.7rem;
    color: purple;
    margin-bottom: 50px;
  }
.title {
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
  }
* {
    box-sizing: border-box;
  }
  
  html, body, #root {
    padding: 0;
    margin: 0;
    height: 100%;
  }
